@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;600;700;900&display=swap);
:root {
  --secondary: #21d07a;
  --white: #fff;
}

body {
  font-family: "Source Sans Pro", sans-serif;
  padding: 0;
  margin: 0;
  background-image: url("https://i.ibb.co/Ry5bq8Q/stars.jpg");
  background-color: black;
  background-repeat: no-repeat;
  height: 100%;
  width: 100%;
  /* background-position: center; */
  background-size: cover;
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}

@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

/* Header Styles */
header {
  background-color: var(--primary);
}

header .inner-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
}

header a {
  color: #fff;
  color: var(--white);
  font-size: 1.25rem;
  text-decoration: none;
  transition: all 0.3s ease;
}

header a:hover {
  opacity: 0.7;
}
header .nav-links li {
  display: inline-block;
  margin-right: 30px;
}

header .nav-links li:last-child {
  margin-right: 0;
}
/* planet card styles */

.result-card .poster-wrapper img,
.filler-poster {
  width: 75px;
  height: 113px;
  background-color: #dbdada;
  border-radius: 5px;
  margin-right: 15px;
  display: block;
  color: transparent;
}

.title {
  display: inline-block;
  margin-top: 10px;
}

.image-container {
  display: inline-block;
}
.planet-container {
  background-color: whitesmoke;
  margin: 1.5rem;
  display: inline-block;
  height: 18rem;
  width: 12rem;
  border: 3px solid darkorange;
  border-radius: 7px;
}

.result-card {
  display: inline-block;
}

.result-card .poster-wrapper img,
.filler-poster {
  width: 11.5rem;
  height: 10rem;
  background-color: #dbdada;
  padding: 3px;
  border-radius: 7px;
  display: block;
}

.result-card .info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.result-card .info .title {
  padding-left: 5px;
  font-size: 1.25rem;
  font-weight: 600;
}

.img {
  width: 12rem;
  height: 230px;
}

.FAW,
.FAW-red {
  background: none;
  border: none;
  float: right;
  margin-top: 5px;
  margin-right: 10px;
}

.FAW-red {
  color: gold;
}

.header h1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  font-size: 2rem;
}

.empty-list {
  color: #c4c4c4;
  font-size: 3rem;
  text-align: center;
}

.spinner {
  position: relative;
  margin: auto;
  box-sizing: border-box;
  /* margin-top: 100px; */
  background-clip: padding-box;
  width: 200px;
  height: 200px;
  border-radius: 100px;
  border: 4px solid rgba(238, 25, 25, 0.3);
  -webkit-mask: linear-gradient(rgba(0, 0, 0, 0.1), #000000 90%);
  transform-origin: 50% 60%;
  transform: perspective(200px) rotateX(66deg);
  -webkit-animation: spinner-wiggle 1.2s infinite;
          animation: spinner-wiggle 1.2s infinite;
}
.spinner:before,
.spinner:after {
  content: "";
  position: absolute;
  margin: -4px;
  box-sizing: inherit;
  width: inherit;
  height: inherit;
  border-radius: inherit;
  opacity: 0.05;
  border: inherit;
  border-color: transparent;
  -webkit-animation: spinner-spin 1.2s cubic-bezier(0.6, 0.2, 0, 0.8) infinite,
    spinner-fade 1.2s linear infinite;
          animation: spinner-spin 1.2s cubic-bezier(0.6, 0.2, 0, 0.8) infinite,
    spinner-fade 1.2s linear infinite;
}

.spinner:before {
  border-top-color: #66e6ff;
}

.spinner:after {
  border-top-color: #f0db75;
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}

@-webkit-keyframes spinner-spin {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes spinner-spin {
  100% {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes spinner-fade {
  20% {
    opacity: 0.1;
  }
  40% {
    opacity: 1;
  }
  60% {
    opacity: 0.1;
  }
}
@keyframes spinner-fade {
  20% {
    opacity: 0.1;
  }
  40% {
    opacity: 1;
  }
  60% {
    opacity: 0.1;
  }
}

